import icons from '../icons';

export default {
  name: 'SavedNotification',
  props: {
    value: {
      type: Boolean,
      default: false,
    },
    error: {
      type: Boolean,
      default: false,
    },
    timeout: {
      type: Number,
      default: 2000,
    },
    showText: {
      type: Boolean,
      default: false,
    },
  },
  methods: {
    showNotification() {
      setTimeout(() => {
        this.$emit('input', false);
      }, this.timeout);
    },
  },
  watch: {
    value(value) {
      if (value) this.showNotification();
    },
  },
  computed: {
    icons() {
      return icons;
    },
  },
};
