import formMixin from '../mixins/formMixin';

export default {
  name: 'ChangePasswordCard',
  mixins: [formMixin],
  data() {
    return {
      currentPassword: '',
      newPassword: '',
      newPassword2: '',
      buttonLoading: false,
      error: false,
      success: false,
    };
  },
  methods: {
    onSubmit() {
      this.changePassword();
    },
    /**
     * This sends the API request to change the password.
     */
    changePassword() {
      this.fail = false;
      this.buttonLoading = true;

      this.$axios.put('/api/profile/password/', {
        current: this.currentPassword,
        new: this.newPassword,
      })
        .then((response) => {
          if (response.data.success === true) {
            this.error = false;
            this.success = true;
          } else {
            this.error = true;
          }
        })
        .catch((error) => {
          this.error = true;
          throw error;
        })
        .finally(() => {
          this.buttonLoading = false;
        });
    },
  },
};
